:root {
    --primary: #ea40a4;
    --buisness: #3A82EE;
    --personal: var(--primary);
    --grey: #888;
    --light: #EEE;
    --dark: #313154;
    --danger: #FF5B57;

    --shadow: 0 1px 3px rgba(0, 0, 0, 0, 0.1);

    --buissness-glow: 0px 0px 4px rgba(58, 130, 238, 0.75);
    --personal-glow: 0px 0px 4px rgba(234, 64, 164, 0.75);
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Play", sans-serif;
}

input:not([type="radio"]):not([type="checkbox"]), button {
    appearance: none;
    border: none;
    outline: none;
    background: none;
    cursor: initial;
}

body {
    background: var(--light);
    color: var(--dark);
}

section {
    margin-top: 20px;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

h3 {
    color: var(--dark);
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 10px;
}

h4 {
    color: var(--grey);
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 10px;
}

.greeting .title {
display: flex;
}

.greeting .title input {
    margin-left: 10px;
    flex: 1 1 0%;
    min-width: 0;
}

.greeting .title,
.greeting .title input {
    color: var(--dark);
    font-size: 1.5rem;
    font-weight: 700;
}

.create-todo input[type="text"] {
    display: flex;
    width: 100%;
    font-size: 18px;
    padding: 10px 10px;
    color: var(--dark);
    background-color: white;
    border-radius: 5px;
    box-shadow: var(--shadow);
    margin-bottom: 10px;
}

.create-todo .options {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    margin-bottom: 10px;
}

.create-todo .options label {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    box-shadow: var(--shadow);
    cursor: pointer;
}

input[type="radio"],
input[type="checkbox"] {
    display: none;
}

.bubble {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid;
}

.bubble.buisness {
    border-color: var(--buisness);
    box-shadow: var(--buissness-glow);
}

.bubble.personal {
    border-color: var(--personal);
    box-shadow: var(--personal-glow);
}

.bubble::after {
    content: "";
    display: block;
    opacity: 0;
    width: 0px;
    height: 0px;
    border-radius: 50%;
    transition: 0.2s ease-in-out;
}

.bubble.buisness::after {
    background-color: var(--buisness);
    box-shadow: var(--buissness-glow);
}

.bubble.personal::after {
    background-color: var(--personal);
    box-shadow: var(--personal-glow);
}

input:checked ~ .bubble::after {
    width: 10px;
    height: 10px;
    opacity: 1;
}

.create-todo .options label div {
    color: var(--dark);
    font-size: 20px;
    margin-top: 10px;
}

.create-todo input[type="submit"] {
    display: block;
    width: 100%;
    font-size: 20px;
    padding: 1rem 1.5rem;
    color: white;
    background-color: var(--primary);
    border-radius: 5px;
    box-shadow: var(--personal-glow);
    cursor: pointer;
    transition: 0.2 ease-in-out;
}

.create-todo input[type="submit"]:hover {
    opacity: 0.75;
}

.todo-list .list {
    margin: 20px 0;
}

.todo-list .todo-item {
    display: flex;
    align-items: center;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: var(--shadow);
    margin-bottom: 10px;
}

.todo-item label {
    display: block;
    margin-right: 10px;
    cursor: pointer;
}

.todo-item .todo-content {
    flex: 1 1 0%;
}

.todo-item .todo-content input {
    color: var(--dark);
    font-size: 20px;
}


.todo-item .actions {
    display: flex;
    align-items: center;
}

.todo-item .actions button {
    display: block;
    padding: 10px;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.todo-item .actions button:hover {
    opacity: 0.75;
}

.todo-item .actions .edit {
    margin-right: 10px;
    background-color: var(--primary);
}

.todo-item .actions .delete {
    background-color: var(--danger);
}

.todo-item.done .todo-content input {
    text-decoration: line-through;
    color: var(--grey);
}